import React from "react";
import {
  Header,
  HeaderName,
  HeaderMenuItem,
  HeaderNavigation,
} from "carbon-components-react";
import { IdeNavigation, IdeSideNavLink } from "@hybrid-cloud/ide-components";

import { ROUTER_CONFIG } from "../assets/Navigation.config.js";
import { NavLink } from "react-router-dom";
import "./LayoutNav.scss";

const ariaAttributes = {
  "aria-labelledby": "#myId",
  "aria-label": "sample aria label to describe the nav",
};

export const LayoutNav = ({ children }) => (
  <React.Fragment>
    <Header aria-label="IBM Toolkit">
      <HeaderName href="#">
        App Connect&nbsp;<span className="header-highlight">Toolkit</span>
      </HeaderName>
      <HeaderNavigation aria-label="IBM Toolkit">
        <HeaderMenuItem>
          <a href="http://org.eclipse.ui.intro/close" class="bx--header__menu-item" tabindex="0">
            <span class="bx--text-truncate--end">Close page</span>
          </a>
        </HeaderMenuItem>
      </HeaderNavigation>
    </Header>
    <IdeNavigation
      ariaAttributes={ariaAttributes}
      navigationLinks={ROUTER_CONFIG.links.map((linkToGen, index) => {
        if (linkToGen.label !== 'Tutorials') {
          return (
            <IdeSideNavLink
              key={`link-${index}-top-level`}
              renderIcon={linkToGen.icon}
              href={linkToGen.link}
            >
              {linkToGen.label}
            </IdeSideNavLink>
          )
        } else {
          return (
            <IdeSideNavLink
              key={`link-${index}-top-level`}
              renderIcon={linkToGen.icon}
              element={NavLink}
              to={{ pathname: linkToGen.path }}
            >
              {linkToGen.label}
            </IdeSideNavLink>
          )
        }
      })}
    >
      <div className="main-content carbon-theme--g10">
        {children}
      </div>
    </IdeNavigation>
    {
      /*
      <IdeNavigation
        ariaAttributes={ariaAttributes}
        navigationLinks={ROUTER_CONFIG.links.map((linkToGen, index) => (
          <IdeSideNavLink
            key={`link-${index}-top-level`}
            renderIcon={linkToGen.icon}
            element={NavLink}
            to={{ pathname: linkToGen.path }}
          >
            {linkToGen.label}
          </IdeSideNavLink>
        ))}
      >
        <div className="main-content carbon-theme--g10">
          {children}
        </div>
      </IdeNavigation>
      */
    }

  </React.Fragment>
);
