/* eslint-disable react/prop-types */
import React, { useEffect, useState } from "react";
import cx from 'classnames';
import "./Tree.scss";
import icons from '../assets/svg/icons/icons.svg';

const TreeLeaf = ({ className, leaf, path }) => (
  <button className={cx('tree-leaf', className)} data-path={`${path}/${leaf.label}`}>
    <svg><use href={`${icons}#${'icon-tree-file'}`}></use></svg>
    {leaf.label}
  </button>
);

const TreeBranch = ({branch, className, path}) => {
  const [open, setOpen] = useState(false);

  const newPath = `${path}/${branch.label}`;

  useEffect(() => {
    setOpen(branch.open);
  }, [branch.open]);

  return (
    <>
      <button className={cx(['tree-branch', className, { 'tree-branch--open': open}])} onClick={() => setOpen(!open)} data-path={newPath}>
        <svg className="tree-branch__chevron"><use href={`${icons}#icon-tree-chevron`}></use></svg>
        <svg><use href={`${icons}#${open ? 'icon-folder-open' : 'icon-folder-closed'}`}></use></svg>
        {branch.label}
      </button>
      <div className="tree-branch__content">
        {
          branch.items.map((item) => {
            if (item.items) {
              return <TreeBranch branch={item} key={`${newPath}/${item.label}`} path={newPath}/>;
            } else {
              return <TreeLeaf leaf={item} key={`${newPath}/${item.label}`} path={newPath} />;
            }
          })
        }
      </div>
    </>
  );
};

export const Tree = ({ tree }) => {
  return tree.map(item => {
    if (item.items) {
      return <TreeBranch className="root" branch={item} key={item.label} path="" />;
    } else {
      return <TreeLeaf className="root" leaf={item} key={item.label} path="" />
    }
  });
};
