import React, { useState, useEffect } from "react";

import "./FlowNode.scss";

import nodeIcons from '../assets/svg/node/icons-node-http.svg';
import ReactResizeDetector from 'react-resize-detector';

const blockClass = 'flow-node';

export const FlowNode = ({ kind, nodeName, nodeType, shape, position}) => {
  const [classes, setClasses] = useState(blockClass);
  const [nodeStyle, setNodeStyle] = useState({});
  const [nodeWidth, setNodeWidth] = useState(0);

  useEffect(() => {
    const shapeClass = shape ? `${blockClass}--${shape}` : '';
    setClasses(`${blockClass} ${shapeClass}`);
  }, [shape]);

  useEffect(() => {
    const newStyles = {};
    if (position) {
      newStyles.transform = `translate(${position.x * 100}px, ${position.y * 100}px)`;
    }

    if (nodeWidth > 0) {
      newStyles['--label-width'] = `${nodeWidth}px`;
    }
    setNodeStyle(newStyles)
  }, [position, nodeWidth]);

  const handleResize = (width) => {
    setNodeWidth(width);
  };

  return (
      <div className={classes} style={nodeStyle}>
        <svg className={`${blockClass}__img`}>
          <use href={`${nodeIcons}#icon-node-${kind}`} />
        </svg>
        <ReactResizeDetector handleWidth onResize={handleResize}>
          <div className={`${blockClass}__text`}>
            <div className={`${blockClass}__name`}>
              <div className={`${blockClass}__name--wide`}>{nodeName}</div>
              <div className={`${blockClass}__name--narrow`}>{nodeName}</div>
            </div>
            <div className={`${blockClass}__type`}>{nodeType}</div>
          </div>
        </ReactResizeDetector>
      </div>
  );
};
