import React, { useState, useEffect } from "react";
// import React, { useState, useRef, useEffect } from "react";
import cx from 'classnames';
import { Toolbar, ToolbarLabel, ToolbarItem, ToolbarButton } from "./Toolbar";
import { FlowNode } from './FlowNode';
import { FlowConnector } from './FlowConnector';
import icons from '../assets/svg/icons/icons.svg';

import "./FlowExerciser.scss";

export const FlowExerciser = ({ className, flowNodeShape }) => {
  const [zoom, setZoom] = useState(100);
  const [customProps, setCustomProps] = useState({});

  const handleZoomIn = () => {
    setZoom(previous => previous < 200 ? previous + 25 : previous);
  };

  const handleZoomOut = () => {
    setZoom(previous => previous > 50 ? previous - 25 : previous);
  };

  useEffect(() => {
    setCustomProps(previous => {
      const newProps = { ...previous };
      newProps['--flow-exerciser--zoom'] = zoom / 100;
      return newProps;
    })
  }, [zoom]);

  return (
    <div className={cx(['flow-exerciser', className])} style={customProps}>
      <Toolbar className="flow-exerciser__toolbar">
        <ToolbarLabel>
          <span>Flow Exerciser</span>
        </ToolbarLabel>
        <ToolbarItem>
          <span className="flow-exerciser__toolbar-label">{zoom}%</span>
          <ToolbarButton small onClick={handleZoomOut} title="Zoom out"><svg><use href={`${icons}#icon-zoom-minus`} /></svg></ToolbarButton>
          <ToolbarButton small onClick={handleZoomIn} title="Zoom in"><svg><use href={`${icons}#icon-zoom-plus`} /></svg></ToolbarButton>
        </ToolbarItem>
      </Toolbar>
      <div className="flow-exerciser__container">
        <div className="flow-exerciser__content" >
          <FlowNode nodeName="HTTP Input 1" nodeType="Http Input" kind="http"  shape={flowNodeShape}/>
          <FlowConnector leftNodes={[{kind: 'square'}, {kind: 'circle', connected: true}, {kind: 'circle'}, {kind: 'circle'}]} rightNodes={[{kind: 'circle', connected: true}]}></FlowConnector>
          <FlowNode nodeName="Map input to output" nodeType="Mapping" kind="mapping"  shape={flowNodeShape}/>
          <FlowConnector leftNodes={[{kind: 'square', connected: true}, {kind: 'circle'}]} rightNodes={[{kind: 'circle', connected: true}]}></FlowConnector>
          <FlowNode nodeName="HTTP reply 1" nodeType="HTTP reply" kind="http"  shape={flowNodeShape}/>
        </div>
      </div>
    </div>
  );
};
