import React, { useState, useEffect } from "react";
import cx from 'classnames';

import "./FlowConnector.scss";

const blockClass = 'flow-connector';

export const FlowConnector = ({ leftNodes, rightNodes }) => {
  const [wireSettings, setWireSettings] = useState({'--left-top': 0, '--right-top': 0});
  const [type, setType] = useState('green');

  useEffect(() => {
    const connectorPosition = (nodes) => {
      // indexes are -3 -2 -1 0 1 2 3 essentially distance from center
      const index = nodes.findIndex(node => node.connected);
      return 2 * index - (nodes.length - 1);
    };
    let leftPos = connectorPosition(leftNodes);
    let rightPos = connectorPosition(rightNodes);

    const newSettings = {};
    newSettings['--left-top'] = `calc(50% + (${leftPos} * 12.5%))`;
    newSettings['--right-top'] = `calc(50% + (${rightPos} * 12.5%))`;
    if (leftPos > rightPos) {
      newSettings['--mid-size'] = 'calc(var(--left-top) - var(--right-top) + 1px)';
      newSettings['--mid-top'] = 'var(--right-top)';
    } else {
      newSettings['--mid-size'] = 'calc(var(--right-top) - var(--left-top) + 1px)';
      newSettings['--mid-top'] = 'var(--left-top)';
    }

    if (leftNodes[0].connected) {
      setType('red');
    } else {
      setType('green');
    }

    setWireSettings(newSettings);
  }, [leftNodes, rightNodes]);

  return (
      <div className={cx([`${blockClass}`, `${blockClass}--${type}`])} style={wireSettings}>
        <div className={`${blockClass}__left`}>
          {leftNodes.map(node => (<div className={cx([`${blockClass}__terminal ${blockClass}__terminal--${node.kind}`, {[`${blockClass}__terminal--connected`]: node.connected}])} />))}
        </div>
        <div className={`${blockClass}__wires`}>
          <div className={`${blockClass}__wire ${blockClass}__wire--left`}></div>
          <div className={`${blockClass}__wire ${blockClass}__wire--middle`}></div>
          <div className={`${blockClass}__wire ${blockClass}__wire--right`}></div>
        </div>
        <div className={`${blockClass}__right`}>
          {rightNodes.map(node => (<div className={cx([`${blockClass}__terminal ${blockClass}__terminal--${node.kind}`, {[`${blockClass}__terminal--connected`]: node.connected}])} />))}
        </div>
      </div>
  );
};
