import React, { useState } from "react";
import { TabPanel } from "./TabPanel.js";
import { Toolbar, ToolbarPanel, ToolbarLabel } from "./Toolbar.js";
// import { Toolbar, ToolbarPanel, ToolbarItem, ToolbarButton, ToolbarLabel } from "./Toolbar.js";
import './PropertiesPanel.scss';
import cx from 'classnames';
// import { TextInput } from "carbon-components-react";

// const tools = ['icon-tools-folder', 'icon-tools-sizing', 'icon-tools-arrows', 'icon-tools-down'];

const PropertiesPanelToolbar = ({className}) => {
  return (
    <Toolbar className={className}>
      <ToolbarPanel>
        <ToolbarLabel>Mapping Node Properties - Mapping</ToolbarLabel>
      </ToolbarPanel>
    </Toolbar>
  );
};

const sections = ['Description', 'Basic', 'Validation', 'Monitoring'];

export const PropertiesPanel = () => {
  const [active, setActive] = useState(1);

  return <TabPanel
    label="Properties"
    label2="Problems"
    toolbar={<PropertiesPanelToolbar />}
  >
    <div className="properties">
      <div className="properties__sections">
        {sections.map((section, index) => (<button
          className={cx(['properties__section', {'properties__section--active': active === index + 1}])}
          onClick={() => setActive(index + 1)}>{section}</button>
        ))}
      </div>
      <div className="properties__content">
        <div className="property">
          <label class="property__label">Mapping Routine *</label>
          <input type="text" class="property__value" value="{default}:ExampleMessageFlow_Mapping" />
        </div>
        <div className="property">
          <label class="property__label">Transaction *</label>
          <input type="text" class="property__value" value="hwuofhue wpfhiuwpf dis" />
        </div>
      </div>
    </div>
  </TabPanel>;
}
