import React, { useState } from "react";
import { MenuBar } from "../components/MenuBar.js";
import { MainToolbar } from "../components/MainToolbar.js";
import { TabPanel } from "../components/TabPanel.js";
// import { Toolbar, ToolbarItem, ToolbarLabel } from "../components/Toolbar.js";
import { MessageFlow } from "../components/MessageFlow.js";
import { ApplicationPanel } from '../components/ApplicationPanel';
import { IntegrationPanel } from '../components/IntegrationPanel';
import { PropertiesPanel } from '../components/PropertiesPanel';

import "./LayoutGrid.scss";

const shapes = ['square', 'circle', 'rectangle', 'stadium'];

export const LayoutGrid = ({ left, main, tutorial, bottom, showMainOnly }) => {
  // const [sections, setSections] = useState({
  //   main: true,
  //   left: false,
  //   bottom: false,
  //   tutorial: false,
  // });
  // useEffect(() => {
  //   setSections({
  //     main: main !== undefined,
  //     left: left !== undefined,
  //     bottom: bottom !== undefined,
  //     tutorial: tutorial !== undefined,
  //   });
  // }, []);
  const [carbonTheme, setCarbonTheme] = useState('carbon-theme--g10');
  const [shapeId, setShapeId] = useState(0);
  const [iconSize, setIconSize] = useState('default');

  const handleDark = () => { setCarbonTheme(previous => previous === 'carbon-theme--g90' ? 'carbon-theme--g100' : 'carbon-theme--g90'); }
  const handleLight = () => { setCarbonTheme( previous => previous === 'carbon-theme--g10' ? 'carbon-theme--white' : 'carbon-theme--g10'); }
  const handleShapeChange = () => {
    if (shapeId + 2 > shapes.length) {
      setShapeId(0);
    } else {
      setShapeId(shapeId + 1);
    }
  };
  const handleIconSize = () => {
    setIconSize(previous => previous === 'default' ? 'large' : 'default');
  };

  return (
    <div
      className={`layout-grid ${!showMainOnly ? "" : "layout-grid--main-only"} ${carbonTheme}`}
    >
      <MenuBar className="layout-grid__menu"  onLight={handleLight} onDark={handleDark} onShapeChange={handleShapeChange} currentShape={shapes[shapeId]} onIconSize={handleIconSize} iconSize={iconSize}/>
      <MainToolbar className="layout-grid__toolbar"/>

      <ApplicationPanel className="layout-grid__left"/>

      <TabPanel className="layout-grid__main" label="Message Flow One" label2="Message Flow Two" toolbar={false}>
        <MessageFlow flowNodeShape={shapes[shapeId]} iconSize={iconSize} />
      </TabPanel>
      {!showMainOnly && (
        <TabPanel className="layout-grid__tutorial" label="Tutorial">
          {tutorial}
        </TabPanel>
      )}

      {!showMainOnly && (
        <React.Fragment>
          <IntegrationPanel className="layout-grid__bottom-left" />
          <PropertiesPanel className="layout-grid__bottom-right" />
        </React.Fragment>
      )}
    </div>
  );
};
