const TreeItems = ({items}) => {
  const result = [];
  for (let i in items) {
    const item = items[i];
    result.push(
      <button className="palette__tree-item" key={`${item.label}-${i}`} type="button">
        {typeof item.src === 'object'
          ? <item.src title={item.label} className="message-flow__palette-icons"/>
          : <img src={item.src} alt={item.label}  className="message-flow__palette-icons" />}
        <span className="palette__tree-item__text">{item.label}</span>
      </button>
    );
  }
  return result;
};

export const Palette = ({ className, iconSize }) => {
  const [collapsed, setCollapsed] = useState(false);

  return (
    <div className={cx(["palette", className, `palette--icon-size--${iconSize}`, { 'palette--collapsed': collapsed}])}>
      <div className="palette__container">
        <Toolbar className="palette__toolbar">
          <span className="palette__toolbar-left">
            <ToolbarLabel className="palette-title">
              <span>Palette</span>
            </ToolbarLabel>
          </span>
          <ToolbarButton onClick={() => setCollapsed(!collapsed)} className="palette__toolbar-show-hide" >
            <Maximize24 className={cx({'palette-icon--hidden': !collapsed })} />
            <Minimize24 className={cx({'palette-icon--hidden': collapsed })} />
          </ToolbarButton>
        </Toolbar>
        <Accordion className="message-flow__palette-accordion">
        <Toolbar className="palette__toolbar palette__toolbar--inner">
          <span className="palette__toolbar-left">
            <ToolbarButton title="Select"><Cursor_116 /></ToolbarButton>
            <ToolbarButton title="Connect"><Draw16 /></ToolbarButton>
            <ToolbarButton title="Note"><RequestQuote16 /></ToolbarButton>
          </span>
          <ToolbarButton>
            <Search16 />
          </ToolbarButton>
        </Toolbar>
          {paletteData.map(section => {
            return [
              section.label ? <div className="message-flow__palette-section">{section.label}</div> : null,
              section.items.map((item) => (
                <AccordionItem
                  key={item.label}
                  className="message-flow__palette-accordion-item"
                  title={
                    <div className="message-flow__palette-title">
                      <div className="message-flow__palette-icons">
                        {
                          // has image not icon
                          typeof item.src === 'object'
                            ? <item.src title={item.label} className="message-flow__palette-icons"/>
                            : <img src={item.src} alt={item.label} className="message-flow__palette-icons"/>
                        }
                      </div>
                      <div className="message-flow__palette-label">
                        {item.label}
                      </div>
                    </div>
                  }
                >
                  <div className="message-flow__palette-accordion-item__content">
                    <TreeItems items={item.items} />
                  </div>
                </AccordionItem>
              ))
            ];
          })}
        </Accordion>
      </div>
    </div>
  );
}
