import React, { useState } from "react";
import cx from 'classnames';
import { Close16 } from "@carbon/icons-react";

import { Toolbar } from "../components/Toolbar.js";
import icons from '../assets/svg/icons/icons.svg';
import file from "../assets/svg/icon-tree-file.svg";

import "./TabPanel.scss";

const Tab = ({label, icon, active, onActivate}) => (
  <div className={cx(['tab-top', { 'tab-top--active': active }])} >
    <button type="button" className="tab-top__left" onClick={() => onActivate()}>
      <img src={icon} className="tab-top__icon" alt='Icon'/>
      <h2 className="tab-top__title">{label}</h2>
    </button>
    <button className="tab-top__close" aria-label="Close panel">
      <Close16 />
    </button>
  </div>
);

export const TabPanel = ({ label, label2, toolbar, children, className }) => {
  const [active, setActive] = useState(1);

  return (
    <div className={["tab-panel", className].join(" ")}>
      <div className="tab-panel__header">
        <div className="tab-panel__tab-container">
          <Tab label={label} icon={file} active={active===1} onActivate={() => setActive(1)}/>
          { label2 && <Tab label={label2} icon={file} active={active===2} onActivate={() => setActive(2)} /> }
        </div>
        <div className="tab-panel__size">
          <svg className="tab-panel__icon"><use href={`${icons}#icon-sizing-min`} /></svg>
          <svg className="tab-panel__icon"><use href={`${icons}#icon-sizing-max`} /></svg>
        </div>
      </div>
      {toolbar !== undefined
        ? toolbar === false
          ? null
          : <nav className="tab-panel__toolbar">{toolbar}</nav>
        : <nav className="tab-panel__toolbar"><Toolbar/></nav>
      }
      <div className="tab-panel__content">{children}</div>
    </div>
  );
}
