import React from "react";
// import React, { useEffect, useState } from "react";
// import { useHistory } from "react-router-dom";
import "./TutorialPreview.scss";

import aTutorial from "../assets/a_tutorial.json";
import { UnorderedList, ListItem } from "carbon-components-react";
import { Time20 } from "@carbon/icons-react";
import allTutorials from '../tutorials_data.json'

const TopicsListItems = ({ topics }) => {
  const result = [];
  for (const topic of topics) {
    result.push(
      <ListItem className="tutorial-preview__" key={topic.title}>
        {topic.title}
      </ListItem>
    );
  }

  return result;
};

const OutcomesListItems = ({ outcomes }) => {
  const result = [];

  for (const outcome of outcomes) {
    result.push(
      <ListItem className="tutorial-preview__outcome" key={outcome.title}>
        {outcome.title}
      </ListItem>
    );
  }

  return result;
};

export const TutorialPreview = ({ name }) => {
  console.log(name)

  // return (
  //     <div>
  //         <div onClick={() => setFile(1)}>file 1</div>
  //         <div onClick={() => setFile(2)}>file 2</div>
  //         <div onClick={() => setFile(3)}>file 3</div>
  //     </div>
  // )
  console.log(allTutorials[name])
  const tutorialData = allTutorials[name]
  const details = (tutorialData && tutorialData.tutorial.pageDetails) || '';
  const common = (tutorialData && tutorialData.tutorial._common) || '';
  const summary = details.summary;

  if (tutorialData) {
    // console.log(`Tutorial previewed ${name}`);
    return (
      <div className="tutorial-preview">
        <div className="tutorial-preview__section">
          <span />
          <p className="tutorial-preview__text">{common.shortDescription}</p>
        </div>
        <div className="tutorial-preview__section">
          <div className="tutorial-preview__schedule">
            <Time20 className="tutorial-preview__icon" />
            <p>{common.durationTime}.</p>
          </div>
        </div>
        <div className="tutorial-preview__section">
          <p className="tutorial-preview__text">{details.topicsName}</p>
          <UnorderedList className="tutorial-preview__list">
            <TopicsListItems topics={details.topicList} />
          </UnorderedList>
        </div>
        <div className="tutorial-preview__section">
          <p className="tutorial-preview__text">{details.outcomesName}</p>
          <UnorderedList className="tutorial-preview__list">
            <OutcomesListItems outcomes={details.outcomeList} />
          </UnorderedList>
        </div>
        <div className="tutorial-preview__section">
          <p className="tutorial-preview__text-header">
            {
              (summary && summary.overviewName) === 'OVERVIEW' ?
              'Details' : summary.overviewName
            }
          </p>
          {summary && summary.sections && summary.sections.map((item, index) => (
            <p key={`item-section:${index}`} className="tutorial-preview__text">
              {item.section}
            </p>
          ))}
        </div>
      </div>
    );
  } else {
    return null
  }

};
