/* eslint-disable react/prop-types */
import React from "react";
import "./MenuBar.scss";

export const MenuBar = ({ className, onLight, onDark, onShapeChange, currentShape, onIconSize, iconSize }) => (
  <nav className={["menu-bar", className].join(" ")} aria-label="eclipse menu">
    <div className="menu-bar__panel">
    {[
      "File",
      "Edit",
      "Flow",
      "View",
      "Palette",
      "Navigate",
      "Search",
      "Project",
      "Run",
      "Window",
      "Help",
    ].map((item) => (
      <button className="menu-bar__item" key={item} type="button">
        {item}
      </button>
    ))}
    </div>
    <div className="menu-bar__panel">
      <span className="menu-bar__label">Icon size:</span>
      <button onClick={onIconSize} className="menu-bar__item">{iconSize}</button>

      <span className="menu-bar__label">Select shape:</span>
      <button onClick={onShapeChange} className="menu-bar__item">{currentShape}</button>

      <span className="menu-bar__label">Select theme:</span>
      <button className="menu-bar__item" onClick={onLight} type="button">
        Light
      </button>
      <button className="menu-bar__item" onClick={onDark} type="button">
        Dark
      </button>
    </div>
  </nav>
);
