import React from "react";

import { Tutorial } from "../components/Tutorial";
import { LayoutGrid } from "../layouts/LayoutGrid";

// import { Link, Tag, ClickableTile } from "carbon-components-react";
import "./TutorialPage.scss";

export const TutorialPage = (route) => {
  return (
    <LayoutGrid
      left={<div className="tutorial-page__left">Left bit of page</div>}
      main={
        <div className="tutorial-page__main">Other content appears here</div>
      }
      tutorial={
        <div className="tutorial-page__tutorial">
          <Tutorial
            name={route.match.params.tutorial}
            preview={false}
            initiallyOpen={
              route.location.search === "?option=green" ||
              route.location.search === "?option=yellow"
                ? "Overview"
                : "Create"
            }
          />
        </div>
      }
      bottom={<div className="tutorial-page__bottom">Bottom bit of page</div>}
    />
  );
};
