import React, { useEffect, useState } from "react";
import "./Toolbar.scss";
import cx from 'classnames';

export const ToolbarLabel = ({ className, children }) => (
  <span className={cx("toolbar__label", className)}>
    {children}
  </span>
);

export const ToolbarButton = ({ className, children, small, onClick }) => (
  <button onClick={onClick} className={cx(['toolbar__button', className, {'toolbar__button--small': small}])}>
    {children}
  </button>
);

export const ToolbarTab = ({ className, children, small, onClick, active }) => (
  <button onClick={onClick} className={cx(['toolbar__tab', className, { 'toolbar__tab--active': active }])}>
    {children}
  </button>
);

export const ToolbarItem = ({ className, item, children, handleClick }) => {
  const [hasChildren, setHasChildren] = useState(false);

  useEffect(() => {
    if (children) {
      setHasChildren(true);
    }
  }, []);

  return (
    <span
      className={[
        "toolbar__item",
        hasChildren ? "" : "toolbar__item--blank",
        className,
      ].join(" ")}
      data-item={item}
      onClick={handleClick}
    >
      {children}
    </span>
  );
};

export const ToolbarPanel = ({ ariaLabel, className, children }) => (
  <div className={["toolbar-panel", className].join(" ")}>
    {children}
  </div>
);

export const Toolbar = ({ ariaLabel, className, children, right }) => (
  <nav className={cx([className, "toolbar", {
      'toolbar--empty': !children,
      'toolbar--right': right
     }])} aria-label={ariaLabel}>
    {children}
  </nav>
);
